.lighter {
  width: 80px;
  border: 1px solid grey; }

.light {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  outline: none; }

.light_red.active {
  background: red; }

.light_yellow.active {
  background: yellow; }

.light_green.active {
  background: green; }

.flexBlock {
  display: flex; }
